import { TIME_UNIT_DICTIONARY } from "../constants";
import { ParsingContext } from "../../../chrono";
import { ParsingComponents } from "../../../results";
import dayjs from "dayjs";
import { matchAnyPattern } from "../../../utils/pattern";
import { AbstractParserWithLeftRightBoundaryChecking } from "./AbstractParserWithWordBoundaryChecking";

const MODIFIER_WORD_GROUP = 1;
const RELATIVE_WORD_GROUP = 2;

export default class UKRelativeDateFormatParser extends AbstractParserWithLeftRightBoundaryChecking {
    innerPatternString(context: ParsingContext): string {
        return (
            `(Ð² Ð¼Ð¸Ð½ÑƒÐ»Ð¾Ð¼Ñƒ|Ñƒ Ð¼Ð¸Ð½ÑƒÐ»Ð¾Ð¼Ñƒ|Ð½Ð° Ð¼Ð¸Ð½ÑƒÐ»Ð¾Ð¼Ñƒ|Ð¼Ð¸Ð½ÑƒÐ»Ð¾Ð³Ð¾|Ð½Ð° Ð½Ð°ÑÑ‚ÑƒÐ¿Ð½Ð¾Ð¼Ñƒ|Ð² Ð½Ð°ÑÑ‚ÑƒÐ¿Ð½Ð¾Ð¼Ñƒ|Ñƒ Ð½Ð°ÑÑ‚ÑƒÐ¿Ð½Ð¾Ð¼Ñƒ|Ð½Ð°ÑÑ‚ÑƒÐ¿Ð½Ð¾Ð³Ð¾|Ð½Ð° Ñ†ÑŒÐ¾Ð¼Ñƒ|Ð² Ñ†ÑŒÐ¾Ð¼Ñƒ|Ñƒ Ñ†ÑŒÐ¾Ð¼Ñƒ|Ñ†ÑŒÐ¾Ð³Ð¾)\\s*` +
            `(${matchAnyPattern(TIME_UNIT_DICTIONARY)})(?=\\s*)`
        );
    }

    innerExtract(context: ParsingContext, match: RegExpMatchArray): ParsingComponents {
        const modifier = match[MODIFIER_WORD_GROUP].toLowerCase();
        const unitWord = match[RELATIVE_WORD_GROUP].toLowerCase();
        const timeunit = TIME_UNIT_DICTIONARY[unitWord];

        if (
            modifier == "Ð½Ð° Ð½Ð°ÑÑ‚ÑƒÐ¿Ð½Ð¾Ð¼Ñƒ" ||
            modifier == "Ð² Ð½Ð°ÑÑ‚ÑƒÐ¿Ð½Ð¾Ð¼Ñƒ" ||
            modifier == "Ñƒ Ð½Ð°ÑÑ‚ÑƒÐ¿Ð½Ð¾Ð¼Ñƒ" ||
            modifier == "Ð½Ð°ÑÑ‚ÑƒÐ¿Ð½Ð¾Ð³Ð¾"
        ) {
            const timeUnits = {};
            timeUnits[timeunit] = 1;
            return ParsingComponents.createRelativeFromReference(context.reference, timeUnits);
        }

        if (
            modifier == "Ð½Ð° Ð¼Ð¸Ð½ÑƒÐ»Ð¾Ð¼Ñƒ" ||
            modifier == "Ð² Ð¼Ð¸Ð½ÑƒÐ»Ð¾Ð¼Ñƒ" ||
            modifier == "Ñƒ Ð¼Ð¸Ð½ÑƒÐ»Ð¾Ð¼Ñƒ" ||
            modifier == "Ð¼Ð¸Ð½ÑƒÐ»Ð¾Ð³Ð¾"
        ) {
            const timeUnits = {};
            timeUnits[timeunit] = -1;
            return ParsingComponents.createRelativeFromReference(context.reference, timeUnits);
        }

        const components = context.createParsingComponents();
        let date = dayjs(context.reference.instant);

        // This week
        if (timeunit.match(/week/i)) {
            date = date.add(-date.get("d"), "d");
            components.imply("day", date.date());
            components.imply("month", date.month() + 1);
            components.imply("year", date.year());
        }

        // This month
        else if (timeunit.match(/month/i)) {
            date = date.add(-date.date() + 1, "d");
            components.imply("day", date.date());
            components.assign("year", date.year());
            components.assign("month", date.month() + 1);
        }

        // This year
        else if (timeunit.match(/year/i)) {
            date = date.add(-date.date() + 1, "d");
            date = date.add(-date.month(), "month");

            components.imply("day", date.date());
            components.imply("month", date.month() + 1);
            components.assign("year", date.year());
        }

        return components;
    }
}
