import dayjs from "dayjs";
import { ParsingContext } from "../../../../chrono";
import { AbstractParserWithWordBoundaryChecking } from "../../../../common/parsers/AbstractParserWithWordBoundary";
import { NUMBER, zhStringToNumber } from "../constants";

const PATTERN = new RegExp(
    "(\\d+|[" +
        Object.keys(NUMBER).join("") +
        "]+|åŠ|å‡ )(?:\\s*)" +
        "(?:ä¸ª)?" +
        "(ç§’(?:é’Ÿ)?|åˆ†é’Ÿ|å°æ—¶|é’Ÿ|æ—¥|å¤©|æ˜ŸæœŸ|ç¤¼æ‹œ|æœˆ|å¹´)" +
        "(?:(?:ä¹‹|è¿‡)?åŽ|(?:ä¹‹)?å†…)",
    "i"
);

const NUMBER_GROUP = 1;
const UNIT_GROUP = 2;

export default class ZHHansDeadlineFormatParser extends AbstractParserWithWordBoundaryChecking {
    innerPattern(): RegExp {
        return PATTERN;
    }

    innerExtract(context: ParsingContext, match: RegExpMatchArray) {
        const result = context.createParsingResult(match.index, match[0]);

        let number = parseInt(match[NUMBER_GROUP]);
        if (isNaN(number)) {
            number = zhStringToNumber(match[NUMBER_GROUP]);
        }

        if (isNaN(number)) {
            const string = match[NUMBER_GROUP];
            if (string === "å‡ ") {
                number = 3;
            } else if (string === "åŠ") {
                number = 0.5;
            } else {
                //just in case
                return null;
            }
        }

        let date = dayjs(context.refDate);
        const unit = match[UNIT_GROUP];
        const unitAbbr = unit[0];

        if (unitAbbr.match(/[æ—¥å¤©æ˜Ÿç¤¼æœˆå¹´]/)) {
            if (unitAbbr == "æ—¥" || unitAbbr == "å¤©") {
                date = date.add(number, "d");
            } else if (unitAbbr == "æ˜Ÿ" || unitAbbr == "ç¤¼") {
                date = date.add(number * 7, "d");
            } else if (unitAbbr == "æœˆ") {
                date = date.add(number, "month");
            } else if (unitAbbr == "å¹´") {
                date = date.add(number, "year");
            }

            result.start.assign("year", date.year());
            result.start.assign("month", date.month() + 1);
            result.start.assign("day", date.date());
            return result;
        }

        if (unitAbbr == "ç§’") {
            date = date.add(number, "second");
        } else if (unitAbbr == "åˆ†") {
            date = date.add(number, "minute");
        } else if (unitAbbr == "å°" || unitAbbr == "é’Ÿ") {
            date = date.add(number, "hour");
        }

        result.start.imply("year", date.year());
        result.start.imply("month", date.month() + 1);
        result.start.imply("day", date.date());
        result.start.assign("hour", date.hour());
        result.start.assign("minute", date.minute());
        result.start.assign("second", date.second());
        return result;
    }
}
